@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
#grid{
    display: grid;
    grid-template-rows: minmax(5rem,auto) minmax(calc(100vh - 5rem - 10rem),auto) minmax(10rem,auto);
    grid-template-areas: "navbar" "content" "footer";

}
#navbar{
    grid-area: navbar;

}
#content{
    grid-area: content;
}
#footer{
    grid-area: footer;
}
*{
    margin: 0;
    padding: 0;
}
.box-element {
    box-shadow: hsl(0, 0%, 60%) 0 0 2px;
    background-color: #fff;
    border-radius: 4px;
    padding: 10px;
}

.any-container{
    padding: 1rem;
}

@media screen and (min-width: 720px) {
    .any-container{
        padding: 2rem 4%;
    }
}
@media screen and (min-width: 960px) {
    .any-container{
        padding: 2rem 6%;
    }
}
@media screen and (min-width: 1080px) {
    .any-container{
        padding: 2rem 11%;
    }
}

.clock-container{
    display:grid;
    margin: 1rem;
    background-color:rgba(159, 151, 243, 0.3);
    font-size: 2rem;
    color: white;
    text-align: center;
    margin-top:2rem;

}
.time{
    font-size: 4rem;
    text-align: center;
    text-shadow: 2px 2px 8px #0e295c;
    color: rgb(127, 82, 156);
    margin-bottom: 1rem;
}
@media screen and (min-width:720px){
    .clock-container{
        margin:1rem 15%;
    }
    .time{
        font-size:5rem;
    }
}

@media screen and (min-width:960px){
    .clock-container{
        margin:1rem 20%;
    }
    .time{
        font-size:6rem;
    }
}

@media screen and (min-width:1080px){
    .clock-container{
        margin:1rem 30%;
    }
    .time{
        font-size: 7rem;
    }
}
.animation-area{
    background:linear-gradient(to left,#552e66,#152c4e);
    width:100%;
    min-height:100vh;
}

.box-area{
    position:absolute;
    top: 0;
    left: 0;
    width:100%;
    height:100%;
    overflow:hidden;
}

.box-area li{
    position:absolute;
    display:block;
    list-style: none;
    background:rgba(250,250,255,0.2);
    -webkit-animation:animate 20s linear infinite;
            animation:animate 20s linear infinite;
    padding:4em;
    bottom:-200px;
}
.box-area li:nth-child(1){
    left: 86%;
    font-size: 10px;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
}
.box-area li:nth-child(2){
    left: 12%;
    font-size: calc(15px / 4);
    -webkit-animation-duration: 10s;
            animation-duration: 10s;
    -webkit-animation-delay:1.5s;
            animation-delay:1.5s;
}
.box-area li:nth-child(3){
    left: 70%;
    font-size: 12.5px;
    -webkit-animation-delay: 5.5s;
            animation-delay: 5.5s;
}
.box-area li:nth-child(4){
    left: 54%;
    font-size: calc(75px / 4);
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
    -webkit-animation-duration: 15s;
            animation-duration: 15s;
}

.box-area li:nth-child(5){
    left: 65%;
    font-size: 5x;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
}

.box-area li:nth-child(6){
    left: 15%;
    font-size: calc(55px / 4);
    -webkit-animation-delay: 3.5s;
            animation-delay: 3.5s;
}
.box-area li:nth-child(7){
    left: 20%;
    font-size: calc(30px / 4);
    -webkit-animation-delay: 10s;
            animation-delay: 10s;
}


@-webkit-keyframes animate {
	0% {
		-webkit-transform: scale(0) translateY(0) rotate(0deg);
		        transform: scale(0) translateY(0) rotate(0deg);
		opacity: 1;
	}
	100% {
		-webkit-transform: scale(1.5) translateY(-800px) rotate(360deg);
		        transform: scale(1.5) translateY(-800px) rotate(360deg);
		opacity: 0;
	}
}


@keyframes animate {
	0% {
		-webkit-transform: scale(0) translateY(0) rotate(0deg);
		        transform: scale(0) translateY(0) rotate(0deg);
		opacity: 1;
	}
	100% {
		-webkit-transform: scale(1.5) translateY(-800px) rotate(360deg);
		        transform: scale(1.5) translateY(-800px) rotate(360deg);
		opacity: 0;
	}
}
@-webkit-keyframes opac {
	0% {
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
}
@keyframes opac {
	0% {
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
}

.box-area li:hover{
    -webkit-animation: opac linear ;
            animation: opac linear ;
}

@media screen and (max-width:720px){
    .box-area li{
        bottom:-150px;
    }
}
.nav-container{
    display:-webkit-flex;
    display:flex;
    -webkit-align-items:center;
            align-items:center;
    -webkit-justify-content:center;
            justify-content:center;
    font-family: Pacifico,'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    height: 100%;
    color:rgb(230, 124, 124);
}
h2,h3,h4,h5,h6,p,p1{
    font-family:sans-serif;
}
.text{
    color: rgba(0,0,10,0.85);
}
h1{
    font-size: clamp(2rem, 2.5vw, 2.5rem);
}
h2{
    font-size: clamp(1.5rem, 2vw, 2rem);
}
h3{
    font-size: clamp(1.1rem, 1.8vw, 1.5rem);
}
h4{
    font-size: clamp(1rem, 1.5vw, 1.3rem);;
}
h5{
    font-size: clamp(0.8rem, 1.3vw, 1.1rem);;
}
p1{
    font-size:clamp(1.1rem,1.2vw,1.3rem)
}
p{
    font-size:clamp(1.2rem,1.2vw,1.5rem)
}



.list-container{
    border-radius:1rem;
    z-index: 30;
    position: relative;
}
.list-container div{
    display:-webkit-flex;
    display:flex;
    -webkit-align-items:center;
            align-items:center;
    -webkit-justify-content:flex-start;
            justify-content:flex-start;
    padding: 0.4rem;
    color: white;
    -webkit-animation: show 800ms linear ;
            animation: show 800ms linear ;
}
input[type="checkbox"]{
    margin: 0 1rem;
}
.list-container div:nth-child(odd){
    background-color: rgba(15, 19, 66, 1);
}
.list-container div:nth-child(even){
    background-color: rgb(23, 179, 252);
}

@media screen and (min-width:720px){
    input[type="checkbox"]{
        margin: 0 2rem;
    }
}

@-webkit-keyframes show{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

@keyframes show{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

@-webkit-keyframes fade{
    0%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}

@keyframes fade{
    0%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}
